import React, { Component } from "react";
import { Nav, NavItem } from "react-bootstrap";

import { highlights, trending } from "../data.json";
import HighlightItem from "./HighlightItem";
import { Panel, ListContainer } from "../Components";
import './Highlight.css';

export default class Highlight extends Component {
    state = {
        highlights: [],
        trending: [],
        activeKey: 1
    }

    componentDidMount() {
        this.setState({ highlights, trending });
    }

    handleSelect = (key) => {
        this.setState({ activeKey: key });
    }

    render() {
        return (
            <div>
                <Nav bsStyle="tabs" activeKey={this.state.activeKey} onSelect={this.handleSelect}>
                    <NavItem eventKey={1}>กระทู้แนะนำโดยสมาชิก</NavItem>
                    <NavItem eventKey={2}>Pantip Trend</NavItem>
                </Nav>
                <Panel>
                    <ListContainer>
                        {this.state.activeKey === 1 && this.state.highlights.map(id => <HighlightItem id={id}/>) }
                        {this.state.activeKey === 2 && this.state.trending.map(id => <HighlightItem id={id} />)}
                    </ListContainer>
                </Panel>
            </div>
        );
    }
}



